import Vue from 'vue';
import Router from 'vue-router';
import Main from '../pages/Main';

import AlertPage from '../pages/AlertPage';
import MessPage from '../pages/MessPage';
import DesCrip from "../components/DesCrip";
import DesModel from "../components/DesModel";
import ReadModel from "../pages/ReadModel";
import WorkPage from "../pages/WorkPage";
import Login from "../pages/Login";
import Register from "../pages/Register";
import AddWork from "../pages/AddWork";
import AddMore from "../pages/AddMore";
import ResWork from "../pages/ResWork";
Vue.use(Router)

var router = new Router({
	routes: [{
			path: '/',
			component: Login
		},
		{
			path: '/login',
			component: Login

		},
		{
			path: '/register',
			component: Register

		},

		{
			path: '/main',
			component: Main

		},

		{
			path: '/alertPage',
			component: AlertPage

		},
		{
			path: '/messPage',
			component: MessPage
		},
		{

			path: "/games/:cid/:model",
			component: DesCrip
		},
		{

			path: "/des/:cid/:tit",
			component: DesModel
		},
		{

			path: "/read/:cid/:tit",
			component: ReadModel
		},
		{

			path: "/WorkPage",
			component: WorkPage
		},
		{

			path: "/addwork",
			component: AddWork
		},
		{

			path: "/addmore",
			component: AddMore
		},
		{

			path: "/reswork",
			component: ResWork
		}
	]
});

export default router;


