
import HomePage from '../components/HomePage'
import TelPage from '../components/TelPage'
import MinePage from '../components/MinePage'
import WorkPage from '../components/WorkPage'

import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
;

export default {

	data: function() {
		return {
			animationType: "",
			isShowTabBar: true,
			pageIndex: 0,

		}
	},
	activated: function() {
		this.initData();

	},
	components: {
		Tabbar,
		TabbarItem,
		MinePage,
		HomePage,
		TelPage,
		WorkPage
	},
	mounted: function() {
		//		console.log(this.$route);
		if(this.$route.path.split("/").length > 2) {
			this.isShowTabBar = false;
		} else {
			this.isShowTabBar = true;
		}
	},
	watch: {
		$route: function(to, from) {
			var toNum = to.path.split("/").length;
			var fromNum = from.path.split("/").length;
			if(toNum == fromNum) {
				//平级切换
				this.animationType = "qqq";
			} else if(toNum > fromNum) {
				//进入
				this.animationType = "routein";
			} else {
				//退出
				this.animationType = "routeout";
			}

			if(toNum > 2) {
				this.isShowTabBar = false;
			} else {
				this.isShowTabBar = true;
			}
		}
	},
	methods: {
		initData: function() {

			this.pageIndex = 0;

		}
	}

}



