export default {
	methods: {
		subs: function() {

			// 密码验证
			var pwd1 = $('#pwd1').val();

			var pwd2 = $('#pwd2').val();

			if(pwd2 != pwd1) {
				layer.msg('两次密码输入不一致', { time: 1500 });
				return false;
			}

			//提交表单

			$('#form1').ajaxSubmit({
				dataType: 'json',
				success: function(res) {
					layer.closeAll();
					if(res.status != 1) {
						layer.confirm(res.msg, {
							btn: ['确定'], //按钮
							closeBtn: 0
						}, function() {
							layer.closeAll();

						});
					} else {

						layer.msg('验证成功', { time: 1500 });

						$('#user_mobile').val(phone);
						location.href = '/main'

					}
				}
			});
			return false;

		}
	}

}



