
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
;
import ReadNone from '../components/ReadNone';
import ReadAll from '../components/ReadAll';
export default {

	components: {

		XHeader,
		Tab,
		TabItem,
		ReadNone,
		ReadAll,
		Badge

	},
	data: function() {
		return {
			pageIndex: 0,
		}
	},
	activated: function() {
		this.initData();

	},
	methods: {
		initData: function() {

			this.pageIndex = 0;

		},
		back: function() {
			window.history.back()
		}
	}
}



