	export default {
		name: 'Login',
		data: function() {
			return {
			
			}
		},
		methods:{
			subs:function  () {
				     var username = $('#login_username').val();
            if(username == ''){
                layer.msg('请输入用户名或手机号', {time: 1500});
                $('#login_username').focus();
                return false;
            }

            var password = $('#login_password').val();
            if(password == ''){
                layer.msg('请输入密码', {time: 1500});
                $('#login_password').focus();
                return false;
            }

            var code = $('#login_code').val();
            if(code == ''){
                layer.msg('请输入验证码', {time: 1500});
                $('#login_code').focus();
                return false;
            }
            layer.load(1, {
                shade: [0.1,'#000'] //0.1透明度的白色背景
            });

            $.ajax({
                url:"{:U('Admin/Login/check')}",
                dataType:'json',
                type:'post',
                data:{

                    'code':code,
                },
                success:function(e){
                    if(e.error){
                        layer.closeAll();
                        layer.msg(e.error, {time: 1500});
//                        window.location.reload();
                    }else{
                        $('#form1').ajaxSubmit({
                            dataType:'json',
                            success:function(res){
                                layer.closeAll();
                                if(res.status !=1){
                                    layer.confirm(res.msg, {
                                        btn: ['确定'], //按钮
                                        closeBtn:0
                                    }, function() {
                                        layer.closeAll();
                                        // $('#username,#password,#verify').val('')
                                        window.location.reload();
                                    });
                                } else {
                                    window.location.href=res.url;
                                }
                            }
                        });
                    }
                }
            })


            return false;
			}
			
			
			
		}

	}



