
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
;
import LocatAlert from '../components/LocatAlert';
import HistoryAlert from '../components/HistoryAlert';
export default {

	components: {

		XHeader,
		LocatAlert,
		HistoryAlert,
		Tab,
		TabItem,
		Badge

	},
	data: function() {
		return {
			pageIndex: 0,
			index: 0,
			getBarWidth: function(index) {
				return(index + 1) * 22 + 'px'
			}

		}
	},
	activated: function() {
		this.initData();

	},
	methods: {
		initData: function() {

			this.pageIndex = 0;

		},
		back: function() {
			window.history.back()
		}
	}
}



