
import XHeader from 'vux/src/components/x-header/index.vue'
;


//******************
export default {
	components: {

		XHeader

	},
	data: function() {
		return {
			projectdatas: [{
				id: 1,
				name: '葡萄'
			}, {
				id: 2,
				name: '芒果'
			}, {
				id: 3,
				name: '木瓜'
			}, {
				id: 4,
				name: '榴莲'
			}],

			peopledata: [{
				id: 1,
				name: '小颖'
			}, {
				id: 2,
				name: 'hover'
			}, {
				id: 3,
				name: '空巢青年三 '
			}, {
				id: 3,
				name: '一丢丢'
			}]
		}

	},
	mounted: function() {
		this.$nextTick(function() {

		})
	},
	watch: {
		projectdatas: {
			handler: function(val, oldval) {

			},
			deep: true
		},
		peopledata: {
			handler: function(val, oldval) {

			},
			deep: true
		}
	},

	methods: {
		drag: function(event) {
			dom = event.currentTarget
		},
		drop: function(event) {
			event.preventDefault();
			event.target.appendChild(dom);
		},
		allowDrop: function(event) {
			event.preventDefault();
		}
	}

};



